.footer-container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.footer-title {
  width: 232px;
  letter-spacing: 0;
  text-transform: uppercase;
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.222;
}

.footer-tagline {
  width: 200px;
  letter-spacing: .04em;
  text-transform: uppercase;
  margin-bottom: 60px;
  font-size: 34px;
  font-weight: 700;
  line-height: 1.206;
}

.footer-social {
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.footer-item-logo:not(:last-child) {
  margin-right: 20px;
}

.footer-item-logo:hover, .footer-item-logo:focus {
  animation: rotate-scale-up .4s linear both;
}

@keyframes rotate-scale-up {
  0% {
    -webkit-transform: scale(1) rotateZ(0);
    transform: scale(1) rotateZ(0);
  }

  50% {
    transform: scale(2)rotateZ(180deg);
  }

  100% {
    transform: scale(1)rotateZ(360deg);
  }
}

.footer-link-logo {
  width: 34px;
  height: 34px;
  background-color: var(--accent);
  fill: var(--background);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.footer-link-logo:hover, .footer-link-logo:focus {
  fill: var(--white-text-color);
}

.footer-telephone {
  letter-spacing: 0;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 40px;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.86;
  display: flex;
}

.footer-telephone-text {
  color: var(--black-text-color);
  font-size: 12px;
  font-weight: 700;
  line-height: 1.86;
}

.footer-telephone-link {
  color: var(--white-text-color);
}

.footer-copyright {
  color: var(--copyright);
  border-top: 1px solid var(--line-color);
  flex-direction: column;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.52;
  display: flex;
}

.footer-underline {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.footer-underline-auth {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

@media screen and (min-width: 480px) {
  .footer-container {
    width: 440px;
  }

  .footer-underline-auth {
    margin-bottom: 24px;
  }
}

@media screen and (min-width: 768px) {
  .footer-container {
    width: 704px;
