  .footer-telephone {
    letter-spacing: .02em;
    font-size: 16px;
  }

  .footer-copyright {
    border-top: 0;
  }

  .container-copyright {
    width: 1168px;
    border-top: 1px solid var(--line-color);
    margin-left: auto;
    margin-right: auto;
    padding-left: 0;
    padding-right: 0;
  }
}

